<template>
  <div class="transaction-detail-table-3">
    <el-table
      :data="data"
      tooltip-effect="dark">
      <el-table-column
        prop="create_time_full"
        :label="$t('assets.transDetail.thead.table3.text1')">
      </el-table-column>
      <el-table-column
        prop="currency_mark"
        :label="$t('assets.transDetail.thead.table3.text2')">
      </el-table-column>
      <el-table-column
        :label="$t('assets.transDetail.thead.table3.text3')">
        <template slot-scope="scope">
          <!-- 到账数量 -->
          {{ scope.row.actual }} {{ scope.row.currency_mark }}
        </template>
      </el-table-column>
      <el-table-column
        :label="$t('assets.transDetail.thead.table3.text4')">
        <template slot-scope="scope">
          {{ scope.row.fee }} {{ scope.row.currency_mark }}
        </template>
      </el-table-column>
      <!-- <el-table-column
        prop="create_time_full"
        :label="$t('assets.transDetail.thead.table3.text5')">
      </el-table-column> -->
      <el-table-column
        :show-overflow-tooltip="true"
        :label="$t('assets.transDetail.thead.table3.text6')">
        <template slot-scope="scope">
          {{ remark(scope.row.remarks) }}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: 'transaction-detail-table-3',
    components: {
    },
    props: {
      data: {
        type: Array,
        default() {
          return []
        }
      }
    },
    data() {
      return {
      }
    },
    methods: {
      type(val) {
        if (val < 0 || val > 3) return this.$t('assets.transDetail.tbody.typeFilter.text6')
        // type值为1,2,3时，显示平台转入，平台转出或资产划转
        const map = [
          this.$t('assets.transDetail.tbody.typeFilter.text1'),
          this.$t('assets.transDetail.tbody.typeFilter.text2'),
          this.$t('assets.transDetail.tbody.typeFilter.text3')
        ]
        return map[val - 1]
      },
      orderType(val) {
        if (val < 0 || val > 1) return this.$t('assets.transDetail.tbody.typeFilter.text6')
        // type值为4时，字段变为order_type，显示买入或卖出
        const map = [
          this.$t('assets.transDetail.tbody.typeFilter.text4'),
          this.$t('assets.transDetail.tbody.typeFilter.text5')
        ]
        return map[val]
      },
      remark(val) {
        // 若没有填写备注，显示“无”
        if (!val) {
          return this.$t('assets.transDetail.tbody.remarkFilter.text1')
        } else {
          return val
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .transaction-detail-table-3{
    
  }
</style>



